import PropTypes from "prop-types";
import React, { useState, useEffect } from "react";
import ProductgridList from "./ProductgridList";
import LeafletMapLayout from '../../components/leaflet-map-layout/LeafletMapLayout';
const ShopProducts = ({ products, layout }) => {
  const [mapMarkers, setMapMarkers] = useState([]);
  useEffect(() => {
    if (layout === 'map') {
      const markers = products.map(prod => {
        return prod && prod.shopRelation ? { 
          name: prod.name, 
          lon: prod.shopRelation.longitude, 
          lat: prod.shopRelation.latitude, 
          url: `${process.env.PUBLIC_URL}/product/${prod.id}`
        } : null;
      })
        .filter(el => el);
      setMapMarkers(markers);
    }
  }, [products, layout]);
  return (
    <div className="shop-bottom-area mt-35">
      {layout === 'map' ? (
        <LeafletMapLayout mapHeight="70vh" markers={mapMarkers} center={{ lon: mapMarkers && mapMarkers[0] ? mapMarkers[0].lon : '', lat: mapMarkers && mapMarkers[0] ? mapMarkers[0].lat : '' }} />
      ) : (
        <div className={`row ${layout ? layout : ""}`}>
          <ProductgridList products={products} spaceBottomClass="mb-25" />
        </div>
      )}
    </div>
  );
};

ShopProducts.propTypes = {
  layout: PropTypes.string,
  products: PropTypes.array
};

export default ShopProducts;
